<template>
  <div>
    <h1
      class="font-black uppercase text-transparent md:text-6xl text-4xl bg-clip-text bg-gradient-to-br from-pink-500 to-purple-500 p-10"
    >
      Collections
    </h1>
    <div
      class="flex items-center justify-center m-3 text-black dark:text-white"
    >
      <div class="flex border-2 rounded">
        <input
          type="text"
          class="px-4 py-2 w-5/6 dark:bg-black"
          placeholder="Search..."
          v-model="search"
        />
        <button class="flex items-center justify-center px-4">
          <svg
            class="w-6 h-6 dark:text-white text-black"
            fill="currentColor"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
          >
            <path
              d="M16.32 14.9l5.39 5.4a1 1 0 0 1-1.42 1.4l-5.38-5.38a8 8 0 1 1 1.41-1.41zM10 16a6 6 0 1 0 0-12 6 6 0 0 0 0 12z"
            />
          </svg>
        </button>
      </div>

      <div class="w-1/8">
        <div class="relative ml-4">
          <!-- Dropdown toggle button -->
          <button
            @click="show = !show"
            class="flex items-center font-semibold justify-between w-full p-2 text-black dark:text-white border-2 rounded-md"
          >
            <span>{{ filter }}</span>
            <svg
              class="w-5 h-5 dark:text-white text-black"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 20 20"
              fill="currentColor"
            >
              <path
                fill-rule="evenodd"
                d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z"
                clip-rule="evenodd"
              />
            </svg>
          </button>

          <!-- Dropdown menu -->
          <div v-show="show" class="absolute left-0 mt-2 w-full">
            <div class="relative">
              <div
                class="rounded-md absolute -inset-0.5 blur bg-gradient-to-r from-pink-600 to-purple-500 animate-pulse"
              ></div>
              <div class="relative">
                <button
                  @click="
                    filter = 'Project Name';
                    show = false;
                  "
                  class="block p-3 text-md rounded-md font-semibold w-full bg-white dark:bg-black dark:text-white mb-1"
                >
                  Project Name
                </button>
                <button
                  @click="
                    filter = 'Artist Name';
                    show = false;
                  "
                  class="block p-3 text-md rounded-md font-semibold w-full bg-white dark:bg-black dark:text-white"
                >
                  Artist Name
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div
      class="grid lg:grid-cols-3 xl:grid-cols-3 grid-cols-2 gap-3 justify-around p-5"
    >
      <div
        v-for="(item, i) in filteredProjects"
        :key="i"
        class="relative duration-500 ease-in-out transform hover:scale-105"
      >
        <div
          class="rounded-md absolute -inset-0.5 blur bg-gradient-to-r from-pink-600 to-purple-500 animate-pulse"
        ></div>
        <div class="relative rounded-md bg-white dark:bg-black">
          <router-link :to="`/aboutcollection/${item.projectId}`">
            <div class="overflow-hidden shadow-lg">
              <img
                class="w-full"
                :src="item.url"
                alt="Sunset in the mountains"
              />
              <div>
                <div
                  class="font-semibold md:text-3xl text-xl m-3 p-3 text-black dark:text-white"
                >
                  {{ item.projectName }}
                </div>
                <div
                  class="font-semibold dark:text-white md:text-lg text-base overflow-hidden m-3 p-3"
                >
                  by {{ item.artist }}
                </div>
              </div>
            </div>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, onMounted, ref, computed } from "vue";
import { CONTRACT_ABI, CONTRACT_ADDRESS } from "@/assets/artKonnection.js";
import Caver from "caver-js";

export default {
  setup() {
    let projectNum = ref(0);
    let nextProjectId = ref(0);
    let projectDetails = reactive([]);

    let filter = ref("Project Name");
    let search = ref("");
    let show = ref(false);
    const filteredProjects = computed(() => {
      return projectDetails.filter((post) => {
        if (filter.value == "Project Name") {
          return post.projectName
            .toLowerCase()
            .includes(search.value.toLowerCase());
        } else if (filter.value == "Artist Name") {
          return post.artist.toLowerCase().includes(search.value.toLowerCase());
        }
      });
    });

    let caver = null;
    let contract = null;

    onMounted(() => {
      caver = new Caver(
        window.klaytn ? window.klaytn : process.env.VUE_APP_RPC_URL
      );
      contract = new caver.klay.Contract(CONTRACT_ABI, CONTRACT_ADDRESS);
      _nextProjectId();

      window.onscroll = () => {
        if (window.innerHeight + window.scrollY >= document.body.offsetHeight) {
          for (let i = 0; i < 3; i++) {
            if (projectNum.value < nextProjectId.value) {
              getDetail(projectNum.value);
              projectNum.value += 1;
            }
          }
        }
      };
    });
    const _projectDetails = async (_projectId) => {
      await contract.methods
        .projectDetails(_projectId)
        .call()
        .then((res) => {
          if (res.projectName.length > 0) {
            const _instance = {
              projectName: res.projectName,
              artist: res.artist,
              projectId: _projectId,
              url: `${process.env.VUE_APP_SERVER_URL}/coverassets/${res.projectName}`,
            };
            projectDetails.push(_instance);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    };
    const getDetail = async (_projectId) => {
      _projectDetails(_projectId);
    };

    const _nextProjectId = async () => {
      await contract.methods
        .nextProjectId()
        .call()
        .then((result) => {
          for (let i = 0; i < 6; i++) {
            getDetail(projectNum.value);
            projectNum.value += 1;
            nextProjectId.value = result;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    };

    return {
      projectDetails,
      filteredProjects,
      search,
      show,
      filter,
    };
  },
};
</script>
